import axios from "axios";
import store from "@/store";

async function join(user) {
  try {
    const response = await axios.post("/member/join", {
      //서버쪽의 dto의 필드명과 동일하게 보내야함
      mid: user.id,
      mname: user.name,
      mpassword: user.password,
      mrole: user.role,
      memail: user.email,
    });
    return response.data.result; //success,
  } catch (error) {
    console.log(error);
    return "fail-network";
  }
}

async function login(user) {
  try {
    const response = await axios.post("/member/login", {
      //서버쪽의 dto의 필드명과 동일하게 보내야함
      mid: user.id,
      mpassword: user.password,
    });
    store.dispatch("saveAuth", { userId: response.data.mid, authToken: response.data.accessToken }); //200으로 정상응답이 왔을 경우
    return "success";
  } catch (error) {
    console.log(error); //401이 되면 얘가 실행됨
    if (error.response) {
      if (error.response.status === 401) {
        //401에러가 떴다면 -> 인증 에러 / 아이디와 비밀번호가 틀렸다
        return "fail-401";
      }
    } else {
      //서버측에서 에러가 나면 error밑에 response 객체가 없음
      return "fail-network";
    }
  }
}

async function logout() {
  console.log("로그아웃");
  try {
    await axios.get("/member/logout");
    store.dispatch("deleteAuth");
  } catch (error) {
    console.log(error);
  }
  console.log("로그아웃 완료");
}

