async function refreshToken() {
  try {
    console.log("aaaaaa");
    if (store.state.authToken !== "") {
      const response = await axios.get("/member/refreshToken");
      store.dispatch("saveAuth", {
        userId: response.data.mid,
        authToken: response.data.accessToken
      });
      return true;
    }
  } catch (error) {
    console.log(error);
  }
  return false;
}

export default {
  join,
  login,
  logout,
  refreshToken,
};
